import React, { useEffect, useState } from "react";
import Header from "../../components/header/header";
import styles from "./home.module.css";
import cameraProvider from "../../providers/cameraProvider";
import announcementProvider from "../../providers/announcementProvider";
import deviceProvider from "../../providers/deviceProvider";
import fastPropertyProvider from "../../providers/fastPropertyProvider";
import SettingsRow from "../../components/settingsRow/settingsRow";
import Toggle from "../../components/toggle/toggle";
import {
  toTitleCase,
  removeUnderscores,
  toSnakeCase
} from "../../utils/stringFormatting";
import Spinner from "../../components/spinner/spinner";

const formatFp = (fp: string) =>
  toTitleCase(removeUnderscores(toSnakeCase(fp)));

type RequestStatus = "NONE" | "PENDING" | "SUCCESS" | "FAILURE";

const Home: React.FC = () => {
  const [comingHomeStatus, setComingHomeStatus] = useState(
    "NONE" as RequestStatus
  );

  const [images, setImages] = useState([] as string[]);
  const [lightStatus, setLightStatus] = useState(null as boolean | null);
  const [config, setConfig] = useState({} as { [key: string]: any });

  useEffect(() => {
    deviceProvider
      .get("livingRoomLight")
      .then(r => setLightStatus(r.status))
      .catch(e => console.error(e));
  }, [setLightStatus]);

  useEffect(() => {
    fastPropertyProvider
      .get()
      .then(setConfig)
      .catch(e => console.error(e));
  }, [setConfig]);

  useEffect(() => {
    async function getImages() {
      const totalCameras = await cameraProvider.getTotalCameras();
      const fetchImages: Promise<string>[] = [];

      for (let i = 0; i < totalCameras; i++) {
        fetchImages.push(
          (async () => {
            try {
              const imageBlob = await cameraProvider.getCameraImage(i);
              const image = URL.createObjectURL(imageBlob);
              return image;
            } catch (e) {
              return "";
            }
          })()
        );
      }

      const newImages = await Promise.all(fetchImages);
      setImages(newImages);
    }
    getImages();
  }, [setImages]);

  function clickComingHome() {
    setComingHomeStatus("PENDING");
    announcementProvider
      .comingHome()
      .then(() => setComingHomeStatus("SUCCESS"))
      .catch(e => {
        setComingHomeStatus("FAILURE");
        console.error(e);
      });
  }
  const clickLightStatus = (status: boolean) => {
    setLightStatus(status);
    deviceProvider.put("livingRoomLight", { status });
  };
  const clickConfig = (key: string) => (value: boolean) => {
    const newConfig = { ...config, [key]: value };
    setConfig(newConfig);
    fastPropertyProvider.put(newConfig);
  };

  const settingsToShow = [
    "livingRoomLightsMotionOn",
    "livingRoomLightsMotionOff"
  ];

  return (
    <React.Fragment>
      <Header />
      <div className={styles.imageContainer}>
        {images.map((image, idx) => (
          <img className={styles.image} key={idx} alt="cam" src={image} />
        ))}
      </div>
      <div className={styles.comingHomeContainer}>
        {comingHomeStatus === "FAILURE" || comingHomeStatus === "NONE" ? (
          <input type="submit" value="Coming Home" onClick={clickComingHome} />
        ) : comingHomeStatus === "PENDING" ? (
          <Spinner />
        ) : null}
        {comingHomeStatus === "SUCCESS" ? (
          <div className={styles.response}>See you soon!</div>
        ) : comingHomeStatus === "FAILURE" ? (
          <div className={styles.responseError}>Please try again.</div>
        ) : null}
      </div>
      <div className={styles.lightsSection}>
        <SettingsRow
          description="Living Room Lights"
          input={
            lightStatus !== null ? (
              <Toggle value={lightStatus} onChange={clickLightStatus} />
            ) : null
          }
        />
        {settingsToShow.map(key => (
          <SettingsRow
            key={key}
            description={formatFp(key)}
            input={
              typeof config[key] === "boolean" ? (
                <Toggle value={config[key]} onChange={clickConfig(key)} />
              ) : null
            }
          />
        ))}
      </div>
    </React.Fragment>
  );
};

export default Home;
